.todo-list > li:hover .tools {
  display: inline-block;
}

.todo-list > li.done {
  color: #999;
}

.todo-list > li.done .text {
  text-decoration: line-through;
  font-weight: 500;
}

.todo-list > li.done .label {
  background: #D2D6DE !important;
}

.todo-list > li.primary {
  border-left-color: #3C8DBC;
}

.todo-list > li.success {
  border-left-color: #00A65A;
}

.todo-list > li.info {
  border-left-color: #00C0EF;
}

.todo-list > li.warning {
  border-left-color: #F39C12;
}

.todo-list > li.danger {
  border-left-color: #DD4B39;
}

.todo-list .handle {
  display: inline-block;
  cursor: move;
  margin: 0 5px;
}

.box-widget {
  border: none;
  position: relative;
}

.widget-user .widget-user-header {
  padding: 20px;
  height: 120px;
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
}

.widget-user .widget-user-username {
  margin-top: 0;
  margin-bottom: 5px;
  font-size: 25px;
  font-weight: 300;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}

.widget-user .widget-user-desc {
  margin-top: 0;
}

.widget-user .widget-user-image {
  position: absolute;
  top: 65px;
  left: 50%;
  margin-left: -45px;
}

.widget-user .widget-user-image > img {
  width: 90px;
  height: auto;
  border: 3px solid #fff;
}

.widget-user .box-footer {
  padding-top: 30px;
}

.widget-user-2 .widget-user-header {
  padding: 20px;
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
}

.widget-user-2 .widget-user-username {
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 25px;
  font-weight: 300;
}

.widget-user-2 .widget-user-desc {
  margin-top: 0;
}

.widget-user-2 .widget-user-username,
.widget-user-2 .widget-user-desc {
  margin-left: 75px;
}

.widget-user-2 .widget-user-image > img {
  width: 65px;
  height: auto;
  float: left;
}

.weather .weather-header {
  margin: 0 0 15px;
}

.weather .weather-today .weather-icon {
  font-size: 5em;
  line-height: 1.8em;
}

.weather .weather-today .weather-text .weather-city,
.weather .weather-today .weather-text .weather-status {
  margin: 0 0 10px;
}

.weather .weather-today .weather-text .weather-city {
  font-size: 2em;
  font-weight: 700;
}

.weather .weather-today .weather-text .weather-status {
  font-weight: 700;
  font-size: 16px;
}

.weather .weather-today .weather-text .weather-degrees {
  font-size: 4em;
}

.mailbox-messages > .table {
  margin: 0;
}

.mailbox-controls {
  padding: 5px;
}

.mailbox-controls.with-border {
  border-bottom: 1px solid #F4F4F4;
}

.mailbox-read-info {
  border-bottom: 1px solid #F4F4F4;
  padding: 10px;
}

.mailbox-read-info h3, .mailbox-read-info h5 {
  margin: 0;
}

.mailbox-read-info h3 {
  font-size: 20px;
}

.mailbox-read-info h5 {
  padding: 5px 0 0;
}

.mailbox-read-time {
  font-size: 13px;
  color: #999;
}

.mailbox-read-message {
  padding: 10px;
}

.mailbox-attachments li {
  float: left;
  width: 200px;
  border: 1px solid #eee;
  margin-bottom: 10px;
  margin-right: 10px;
}

.mailbox-attachment-name {
  font-weight: bold;
  color: #666;
}

.mailbox-attachment-icon,
.mailbox-attachment-info,
.mailbox-attachment-size {
  display: block;
}

.mailbox-attachment-info {
  padding: 10px;
  background: #F4F4F4;
}

.mailbox-attachment-size {
  font-size: 12px;
  color: #999;
}

.mailbox-attachment-icon {
  padding: 20px 10px;
  text-align: center;
  font-size: 65px;
  color: #666;
}

.mailbox-attachment-icon.has-img {
  padding: 0;
}

.mailbox-attachment-icon.has-img > img {
  max-width: 100%;
  height: auto;
}

body.lockscreen {
  background: #D2D6DE;
}

.lockscreen-logo {
  font-size: 35px;
  text-align: center;
  margin-bottom: 25px;
  font-weight: 300;
}

.lockscreen-logo a {
  color: #444;
}

.lockscreen-wrapper {
  max-width: 400px;
  margin: 10% auto 0;
}

/* User name [optional] */
.lockscreen .lockscreen-name {
  text-align: center;
  font-weight: 600;
}

/* Will contain the image and the sign in form */
.lockscreen-item {
  -webkit-border-radius: 4px;
  border-radius: 4px;
  background-clip: padding-box;
  /* stops bg color from leaking outside the border: */
  padding: 0;
  background: #fff;
  position: relative;
  margin: 10px auto 30px;
  width: 290px;
}

/* User image */
.lockscreen-image {
  position: absolute;
  z-index: 10;
  left: -10px;
  top: -25px;
  padding: 5px;
  background: #fff;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  background-clip: padding-box;
  /* stops bg color from leaking outside the border: */
}

.lockscreen-image > img {
  width: 70px;
  height: 70px;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  background-clip: padding-box;
